<template>
<div>
    <swiper ref="mySwiper" :options="swiperOptions">
    <swiper-slide> 

    </swiper-slide>
    </swiper>
    <div class="mui-content">
        <swiper ref="mySwiper" :options="swiperOptions">
            <swiper-slide v-for="(item1,index1) in pages" :key=index1> 
                <ul class="mui-table-view mui-grid-view mui-grid-9">
		            <router-link tag="li" to="/Banner" v-for="item2 in item1" :key="item2.id" class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3"><a href="#">
		                    <img class="iconImage" :src="item2.iconImg" alt="">
		                    <div class="mui-media-body">{{item2.desc}}</div></a></router-link>
		        </ul> 
            </swiper-slide>
        </swiper>
	</div>
</div>
  
</template>

<script>
export default {
    name:"HomeIcons",
    data(){
        return {
            swiperOptions:{},
        }

    },
    props:{
      iconList:Array
    },
    computed:{
        pages(){
            var pages=[];
            this.iconList.forEach((item,index)=>{
                var page=Math.floor(index/10);
                if(!pages[page]){
                    pages[page]=[]
                }
                pages[page].push(item);
            })
            return pages;
            }
        }
    }
</script>

<style scoped  lang="stylus">
.mui-content >>> .iconImage
  width 50%
.mui-content .mui-table-view:first-child
  margin-top :0px
.mui-grid-view.mui-grid-9
  background-color:white;
  border :0px;
.mui-grid-view.mui-grid-9 .mui-table-view-cell
  border:0px;
  padding:0px;
.mui-col-xs-4
  width 20%
.mui-table-view.mui-grid-view .mui-table-view-cell .mui-media-body
  margin-top 0px
  font-size 0.8rem
</style>


