<template>
<div class="banner-box">
    <div v-for="item in goodsDetail" :key="item.id" class="goods-box">
          <div class="left-img">
              <img  class="goods-img" :src=item.imgsrc alt="">
          </div>
          <div class="right-goods">
              <div class="goods-desc">
                  <span>{{item.desc}}</span>
              </div>
              <div class="box">
                  <span class="left"></span>
                  <span class="right">31%</span>
              </div>
              <span class="pindan">{{item.pindan}}</span>
              <div class="goods-price">
                  <span class="price">{{item.price}}</span>
                  <span class="yuanjia">{{item.yuanjia}}</span>
              </div>
              <div class="click">
                  马上抢>
              </div>
          </div>
          <div class="fenge"></div>
      </div>
      <div class="bottom"></div>
</div>
</template>

<script>
export default {
    name:"BannerMiddle",
    data(){
        return{
            goodsDetail:[
                {id:"1",desc:"儿童口罩一次性三层熔喷布防护防尘透气加厚学生宝宝小孩50只装",pindan:"已抢877件",price:"￥10.9",yuanjia:"￥19.9",imgsrc:"https://t00img.yangkeduo.com/goods/images/2020-05-26/e5a491f7570699b710aa7f9fbef42ebe.jpeg?imageMogr2/sharpen/50/strip%7CimageView2/2/w/350/q/70/format/webp"},
                {id:"2",desc:"美容仪器李佳琪琦推荐家用脸部按摩洗脸仪清洁面部毛孔导出导入仪",pindan:"已抢43件",price:"￥16.9",yuanjia:"￥59",imgsrc:"https://t00img.yangkeduo.com/goods/images/2020-05-24/281ddcd8c6b7fd322dc06215713b8914.jpeg?imageMogr2/sharpen/50/strip%7CimageView2/2/w/350/q/70/format/webp"},
                {id:"3",desc:"米缇贝蒂小款个性百搭单肩包女欧美简约纯色风斜跨包包时尚链条包",pindan:"已抢201件",price:"￥14.5",yuanjia:"￥62",imgsrc:"https://t00img.yangkeduo.com/goods/images/2020-05-22/e384d099043458a80798e940d6e0ba13.jpeg?imageMogr2/sharpen/50/strip%7CimageView2/2/w/350/q/70/format/webp"},
                {id:"4",desc:"usb迷你静音小风扇可充电随身手持便携式桌面学生宿舍床上大风力",pindan:"已抢60件",price:"￥6.2",yuanjia:"￥88",imgsrc:"https://t00img.yangkeduo.com/goods/images/2020-05-25/14e6f0b264cd0b6650fcd3cf05ec382f.jpeg?imageMogr2/sharpen/50/strip%7CimageView2/2/w/350/q/70/format/webp"},
                {id:"5",desc:"2020夏季新款网红雪纺碎花吊带背心上衣韩版宽松洋气百搭学生外穿",pindan:"已抢32件",price:"￥7.5",yuanjia:"￥33",imgsrc:"https://t00img.yangkeduo.com/goods/images/2020-05-22/b1dead751dac408b750e9b5c9f6da2d0.jpeg?imageMogr2/sharpen/50/strip%7CimageView2/2/w/350/q/70/format/webp"},


            ]
        }
    }
}
</script>

<style scoped>
.banner-box{
    padding-top: 3.5rem;
}
.goods-box{
    display: flex;
    height:0px;
    padding-bottom:45%;
    overflow:hidden;

}

.goods-box .left-img{
    flex: 1;
    width:10rem;
    height:10rem;
    float: left;
}
.goods-box .left-img .goods-img{
    width:10rem;
    height:10rem;
}
.goods-box .right-goods{
    width:13rem;
    height:10rem;
    float:right;
}
.goods-box .right-goods .goods-desc{
    font-size: .9rem;
    width:13rem;
    height: 2.4rem;
    overflow: hidden;
}
.goods-box .right-goods .goods-price .price{
    float: left;
    margin-right: .1rem;
    color: red;
    font-weight: bold;
    font-size: 1.2rem;
    margin-top: 4.1rem;
}
.goods-box .right-goods .goods-price .yuanjia{
    float: left;
    color: #9c9c9c;
    font-size: 0.8rem;
    text-decoration: line-through;
    margin-top: 4.2rem;
}
.box{
    position: relative;
    display: inline-block;
    margin-top: 1rem;
    border: 0.07rem solid #e02e24;
    height: 1rem;
    width: 8rem;
    border-radius: 0.3rem;
    background-color: pink;
}
.box .left{
    display: inline-block;
    height: 1rem;
    width: 3rem;
    border-radius: 0.25rem;
    background-color: #e02e24;
}
.box .right{
    position: absolute;
    display: inline-block;
    text-align: center;
    line-height: 1.1rem;
    font-size: 0.9rem;
    margin-bottom: 1rem;
    left: 3.5rem;
    color: #fff;
}
.click{
    height: 2rem;
    width: 5rem;
    background-color: #e02e24;
    text-align: center;
    line-height: 2rem;
    float: right;
    margin-top: 3.5rem;
    margin-right: 0.6rem;
    border-radius: 0.3rem;
    color: white;
    font-size: 0.9rem;
}
.pindan{
    font-size: 0.8rem;
    color: #9c9c9c;
}
.fenge{
    width: 100%;
    height: 2px;
    background-color: pink;
    margin-bottom: 10px;
}
.bottom{
    width: 100%;
    height: 3rem;
}
</style>


