<script>
import mui from "../../../mui/js/mui.js"
mui('.mui-scroll-wrapper').scroll({
	deceleration: 0.0005 //flick 减速系数，系数越大，滚动速度越慢，滚动距离越小，默认值0.0006
});
export default {
    name:"HomeHeader"
}
</script>

<style scoped>
*{
	touch-action: pan-y;
}
.mui-content{
	background-color: white;
}
.mui-active{
	color: #e02e24 !important;
}
</style>


