<template>
  <div class="swiper-box">
      <swiper ref="mySwiper" :options="swiperOptions">
    <swiper-slide > 
        <img class="swiper-img" src="https://t00img.yangkeduo.com/goods/images/2020-01-17/bb16df99-9492-4cd2-ad80-c555114aaebb.jpg" alt="">
    </swiper-slide>
    <swiper-slide > 
        <img class="swiper-img" src="https://t00img.yangkeduo.com/goods/images/2020-01-17/279223dd-311d-4980-898a-b5ce9ef6a5cd.jpg" alt="">
    </swiper-slide>
  </swiper>
  </div>
</template>

<script>
export default {
    name:'GoodsSwiper',
    data(){
        return{
            swiperOptions:{
                loop:true,
                 autoplay: {

                delay: 2500,

                stopOnLastSlide: false,

                /* 触摸滑动后是否继续轮播 */

                 disableOnInteraction: false

        },
        speed:800,
        direction: "horizontal",
            }
        }
    }
}
</script>

<style scoped>
.swiper-box{
    height: 0px;
    padding-bottom: 100%;
    overflow: hidden;
}
.swiper-img{
    width: 100%;
}
</style>


