<template>
  <div>
      <router-link tag="div" to="/GoodsDetail" v-for="item in recoData" :key="item.id" class="goods-box">
          <div class="left-img">
              <img  class="goods-img" :src="item.reconImg" alt="">
          </div>
          <div class="right-goods">
              <div class="goods-desc">
                  <span>{{item.recoDesc}}</span>
              </div>
              <div class="goods-price">
                  <span class="price">{{item.recoPrice}}</span>
                  <span class="pindan">{{item.recoPin}}</span>
              </div>
              <div class="user-img">
                  <img class="img-one" :src="item.userimg1" alt="">
                  <img class="img-two" :src="item.userimg2" alt="">
              </div>
          </div>
      </router-link>
      <div class="top"></div>
  </div>
</template>

<script>
export default {
    name:"HomeGoods",
    props:{
        recoData:Array
    }
}
</script>

<style scoped >
.goods-box{
    display: flex;
    height:0px;
    padding-bottom:45%;
    overflow:hidden;
}

.goods-box .left-img{
    flex: 1;
    width:10rem;
    height:10rem;
    float: left;
}
.goods-box .left-img .goods-img{
    width:10rem;
    height:10rem;
}
.goods-box .right-goods{
    width:13rem;
    height:10rem;
    float:right;
}
.goods-box .right-goods .goods-desc{
    font-size: .9rem;
    width:13rem;
    height: 2.4rem;
    overflow: hidden;
    margin-bottom: 6rem;
}
.goods-box .right-goods .goods-price .price{
    float: left;
    margin-right: .3rem;
    color: red;
    font-weight: bold;
    font-size: 1.2rem;
}
.goods-box .right-goods .goods-price .pindan{
    font-size: .8rem;
    float: left;
}
.goods-box .right-goods .user-img{
    float: right;
    margin-right: 0.1rem;
    margin-bottom: 0.5rem;
}
.goods-box .right-goods .user-img .img-one,.img-two{
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 50%;

}
.top{
    height: 4rem;
    width: 100%;
}
</style>


